@import "variables";

$size: 48 !default;
@mixin ico-background-image($name,$size) {
    background-image: url("#{$baseurl}/assets/images/simple-icons/#{$name}-#{$size}.png");
}

.social-links {
    a {
        text-indent: -9999em;
        opacity: 0.3;
        background-repeat: no-repeat;
        background-position: center center;
        filter: alpha(opacity=30);

        &:hover {
            -webkit-transition: all .3s ease-out;
            -moz-transition: all .3s ease-out;
            -o-transition: all .3s ease-out;
            transition: all .3s ease-out;
            opacity: 1.0;
            filter: alpha(opacity=100);
        }
    }

    .ico-gmail {
        @include ico-background-image(gmail, $size);
        background-color: var(--gmail-bg-color);
    }

    .ico-leetcode {
        @include ico-background-image(leetcode, $size);
        background-color: var(--leetcode-bg-color);
    }

    .ico-github {
        @include ico-background-image(github, $size);
        background-color: var(--github-bg-color);
    }

    .ico-douban {
        @include ico-background-image(douban, $size);
        background-color: var(--douban-bg-color);
    }

    .ico-stackoverflow {
        @include ico-background-image(stackoverflow, $size);
        background-color: var(--stackoverflow-bg-color);
    }

    .ico-nowcoder {
        @include ico-background-image(nowcoder, $size);
        background-color: var(--nowcoder-bg-color);
    }
}