@import "variables";

@font-face {
    font-family: "Junge Regular";
    font-weight: normal;
    font-style: normal;
    font-display: block ;
    src: url("#{$baseurl}/assets/libs/fonts/Junge/junge-regular-webfont.eot");
    src: url("#{$baseurl}/assets/libs/fonts/Junge/junge-regular-webfont.eot?#iefix") format("embedded-opentype"),
    url("#{$baseurl}/assets/libs/fonts/Junge/junge-regular-webfont.woff") format("woff"),
    url("#{$baseurl}/assets/libs/fonts/Junge/junge-regular-webfont.ttf") format("truetype"),
    url("#{$baseurl}/assets/libs/fonts/Junge/junge-regular-webfont.svg#jungeregular") format("svg");
}

@font-face {
    font-family: "Font Awesome 6 Free Regular";
    font-weight: normal;
