@if $cdn-jsdelivr-enabled {
    /*字体图标*/
    @import url("https://cdn.jsdelivr.net/npm/@fortawesome/fontawesome-free/css/all.min.css");
    @import url("https://cdn.jsdelivr.net/npm/normalize.css/normalize.min.css");
} @else {
    /*字体图标*/
    @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/#{$cdn-font-awesome-version}/css/all.min.css");
    @import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/#{$cdn-normalize-version}/normalize.min.css");
}